.cruises {
  padding: 120px 15px;
}

.cruises__wrapepr {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
}

.cruises__title {
  margin: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  color: $color-tangaroa;
}

.cruises__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  max-width: 1130px;
}

.cruises__item {
  position: relative;
  overflow: hidden;
  width: 260px;
  min-height: 386px;
  background-color: $color-periwinkle;
  background: $tangaroa-to-tangaroa;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: flex-end;

}

.cruises__item--first {
  background-image: url("../../img/content/catalog/cruise-1-img-desktop.jpg");
}

.cruises__item--second {
  background-image: url("../../img/content/catalog/cruise-1-img-desktop.jpg");
}

.cruises__item--third {
  background-image: url("../../img/content/catalog/cruise-3-img-desktop.jpg");
}

.cruises__item--fourth {
  background-image: url("../../img/content/catalog/cruise-4-img-desktop.jpg");
}

.cruises__item--fifth {
  background-image: url("../../img/content/catalog/cruise-5-img-desktop.jpg");
}

.cruises__item--sixth {
  background-image: url("../../img/content/catalog/cruise-6-img-desktop.jpg");
}

.cruises__item--seventh {
  background-image: url("../../img/content/catalog/cruise-7-img-desktop.jpg");
}

.cruises__item--eighth {
  background-image: url("../../img/content/catalog/cruise-8-img-desktop.jpg");
}


.cruises__item-title {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: $color-alice-blue;
  padding-bottom: 30px;
  padding-left: 30px;
}

.cruises__item-info-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  bottom: -100%;
  display: flex;
  z-index: 5;
  flex-direction: column;
  justify-content: space-between;
  background-color: $color-tangaroa;
  padding: 30px 25px;
  transition: bottom .3s linear;
}

.cruises__item:hover .cruises__item-info-wrapper,
.cruises__item:focus .cruises__item-info-wrapper {
  bottom: 0;
}

.cruises__item:hover .cruises__item-title,
.cruises__item:focus .cruises__item-title {
  display: none;
}

.cruises__item-info-inner {
  display: flex;
  flex-direction: column;
}

.cruises__item-info-title {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: $color-alice-blue;
  padding-bottom: 13px;
}

.cruises__item-subtitle {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: $color-alice-blue;
}

.cruises__item-text {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
  color: $color-quartz;
  padding-bottom: 12px;
}

.cruises__item-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 40px;
  background: $color-alice-blue;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: $color-tangaroa;
  cursor: pointer;
  z-index: 10;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.8;
    }
  }
}


